import JSZip from 'jszip';
import parseInf from './InfParser.js';

export default class ZipRepo {
    loadZipRepo( buf ){
	let ok, nok;

	let p = new Promise( (_ok, _nok ) => {
	    ok = _ok;
	    nok = _nok;
	});
	    
	JSZip.loadAsync( buf )
	    .catch( ex => nok(ex) )
	    .then( z => {
		let hexRefs = Object
		    .keys(z.files)
		    .map( f => z.files[f] )
		    .filter( f => !f.dir && /\.HEX$|\.BIN$/i.test(f.name) );
		
		Promise.all(
		    hexRefs
			.map( file => file
			      .async(
				  file
				      .name
				      .split(".")
				      .pop()
				      .toLowerCase() == "hex" ? "text" : "arraybuffer"
			      )
			    )
		)
		    .catch( ex => nok(ex) )
		    .then( hexes => {

			let infRefs = Object
			    .keys(z.files)
			    .map( f => z.files[f] )
			    .filter( f => !f.dir && /\.INF$|\.JSON$/i.test(f.name) );
			
			Promise.all( infRefs.map( file => file.async("arraybuffer") ) )
			    .catch( ex => nok(ex) )
			    .then( infs => {

				let hexIndex;
				hexIndex = hexRefs.reduce( (acc, ref, i) => {
				    acc[ ref.name.replace(/\.HEX$|\.BIN$/i, '') ] = {
					raw:hexes[i],
					rawType: "application/x-" + ref.name.split(".").pop().toLowerCase()
				    };
				    return acc;
				}, {} );

				let items = infRefs.map( (infRef, i) => {
				    let ext = infRef.name.split(".").pop();
				    let name = infRef.name.replace(/\.(INF|JSON)$/i, '');
				    let hex = hexIndex[name];
				    if( !hex ) return null;
				    if( ext == "INF" )
					return parseInf(
					    name,
					    new Uint8Array(infs[i]),
					    hex.raw,
					    hex.rawType
					);
				    else{
					let json = {device:"arduboy"};
					try{
					    let td = new TextDecoder();
					    json = JSON.parse(td.decode(infs[i]));
					}catch(ex){}
					return Object.assign( hex, json );
				    }
				});

				ok( items );
				
			    });

		    });

	    });

	return p;
    }

}



