export default class SPI0 {
    registers = {
	0x40040000:{name:'CR0'},
	0x40040004:{name:'CR1'},
	0x40040008:{name:'DR'},
	0x4004000C:{name:'SR'},
	0x40040010:{name:'CPSR'},
	0x40040014:{name:'IMSC'},
	0x40040018:{name:'RIS'},
	0x4004001C:{name:'MIS'},
	0x40040020:{name:'ICR'},
	0x40040024:{name:'DMACR'}
    }
}



